body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-align-content: center;
          align-content: center;
  background: #000000;
  box-sizing: border-box;
}

.textarea-container {
  box-sizing: border-box;
  background: #ffffff;
  display: inline-block;
  width: 50%;
  padding: 10px;
  margin: 2px;
  border: 1px solid black;
  height: auto;
}

#editor {
  box-sizing: border-box;
  width: 100%;
  height: 95%;
}

.preview-container {
  box-sizing: border-box;
  background: #ffffff;
  display: inline-block;
  width: 50%;
  padding: 10px;
  margin: 2px;
  border: 1px solid black;
  height: auto;
}

@media screen and (max-width: 800px) {
  .App {
    display: block;
  }

  #editor {
    height: 40vh;
  }

  .textarea-container {
    display: block;
    width: calc(100% - 2px);
    height: 50vh;
    margin: 2px;
    border: none;
  }

  .preview-container {
    display: block;
    width: calc(100% - 2px);
    height: auto;
    max-height: 49vh;
    overflow-y: scroll;
    margin: 2px;
    padding-right: 1rem;
    padding-left: 1rem;
    border: none;
  }

  #editor-heading,
  #preview-heading {
    font-size: 1rem;
    padding: 2px;
    margin: 0;
  }
}

